import React, { useState } from "react";
import { Row, Col } from "antd";
import "antd/dist/antd.css";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";

export function HomePage() {
  // Declare a new state variable, which we'll call "count"
  const [count, setCount] = useState(0);

  const divStyle = {
    color: "gray",
    backgroundColor: "white"
  };
  const buttonStyle = {
    color: "gray",
    backgroundColor: "transparent",
    border: "none"
  };
  const SpaceStyle = {
    color: "white",
    backgroundColor: "white"
  };
  return (
    <div>
      <Row>
        <Col span={24} style={SpaceStyle}>
          {" "}
          -{" "}
        </Col>
        <Col span={4}></Col>
        <Col span={16}>
          <img src="elderly2.png" alt="elder" width="250" />
        </Col>

        <Col span={4}></Col>
        <Col span={8}>
          <Link to="/notifications">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="medicine.png" alt="Medicine" width="80" />
              <br />
              แจ้งเตือน
              <br />
              การรับประทานยา
            </button>
          </Link>
        </Col>
        <Col span={8}>
          <Link to="/health_checks">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="record.png" alt="Form" width="67" />
              <br />
              บันทึกการตรวจสุขภาพ
            </button>
          </Link>
        </Col>
        <Col span={8}>
          <Link to="/appointments">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="calendar3.png" alt="Appointment" width="85" />
              <br />
              แจ้งเตือน
              <br />
              วันนัดครั้งถัดไป
            </button>
          </Link>
        </Col>

        <Col span={8}>
          <Link to="/nutrition">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="flag123.png" alt="Nutrition" width="100" />
              <br />
              โภชนาการ
            </button>
          </Link>
        </Col>
        <Col span={8}>
          <Link to="/ingredient">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="cook.png" alt="Menu" width="105" />
              <br />
              Menu
              <br />
              สูตรอาหาร
            </button>
          </Link>
        </Col>
        <Col span={8}>
          <Link to="/water">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="bottle.png" alt="Drinking" width="60" />
              <br />
              ปริมาณน้ำ
              <br />
              ที่ควรดื่มต่อวัน
            </button>
          </Link>
        </Col>
        <Col span={8}>
          <Link to="/rests">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="sleep.png" alt="Sleeping" width="120" />
              <br />
              แจ้งเตือนการนอน
            </button>
          </Link>
        </Col>
        <Col span={8}>
          <Link to="/question">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="light.png" alt="Knowlegde" width="59" />
              <br />
              มาเพิ่มความรู้กันเถอะ
            </button>
          </Link>
        </Col>
        <Col span={8}>
          <Link to="/bmi">
            <button onClick={() => setCount(count + 1)} style={buttonStyle}>
              <img src="BMI.png" alt="BMI" width="70" />
              <br />
              คำนวณค่า
              <br />
              ดัชนีมวลกาย
            </button>
          </Link>
        </Col>
      </Row>
    </div>
  );
}
