import React, { useState } from "react";
import "antd/dist/antd.css";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import { TitlePage } from "./TitlePage";
import { HomePage } from "./HomePage";
import { Notification } from "./Notification";
import { NotificationList } from "./NotificationList";
import { Health_check } from "./Health_check";
import { Health_checkList } from "./Health_checkList";
import { Appointment } from "./Appointment";
import { AppointmentList } from "./AppointmentList";
import { NutritionList } from "./NutritionList";
import { Nutrition_subjectList } from "./Nutrition_subjectList";
import { Nutrition_body } from "./Nutrition_body";
import { IngredientList } from "./IngredientList";
import { Ingredient_subjectList } from "./Ingredient_subjectList";
import { Ingredient_body } from "./Ingredient_body";
import { QuestionList } from "./QuestionList";
import { Question_subjectList } from "./Question_subjectList";
import { Question_body } from "./Question_body";
import { Water } from "./Water";
import { Rest } from "./Rest";
import { Rest_next } from "./Rest_next";
import { Bmi } from "./Bmi";
import { initDB, useIndexedDB } from "react-indexed-db";
import { DBConfig } from "./DBConfig";
import { useInterval } from "rooks";
import moment from "moment";
initDB(DBConfig);
navigator.serviceWorker.register("sw.js");

export default function App() {
  const showNotification = (title, options) => {
    window.Notification.requestPermission((result) => {
      if (result === "granted") {
        navigator.serviceWorker.ready.then((registration) => {
          registration.showNotification(title, options);
        });
      }
    });
  };

  const AcreateAndShowNotification = (title, body) => {
    const f = new Intl.DateTimeFormat("th");
    const now = f.format(Date.now());

    const options = {
      tag: now,
      body: body,

      renotify: true
    };
    showNotification(title, options);
  };
  const NotifyAppointment = () => {
    const { getAll } = useIndexedDB("appointment");
    getAll().then((appointments) => {
      for (const appointment of appointments) {
        console.log(`appointment=${appointment}`);
        let AppointmentMoment = moment(
          appointment.day + " " + appointment.time,
          "DD/MM/YYYY HH:mm"
        );
        let d = AppointmentMoment.diff(moment());
        console.log(d);
        if (d <= 600000 && d >= -60000) {
          AcreateAndShowNotification("Appointment", appointment.note);
        }
      }
    });
  };
  const NotifyNotification = () => {
    const { getAll } = useIndexedDB("notification");
    getAll().then((notifications) => {
      for (const notification of notifications) {
        console.log(`notification=${notification}`);
        let NotificationMoment = moment(
          moment().format("DD/MM/YYYY") + " " + notification.time,
          "DD/MM/YYYY HH:mm"
        );
        console.log(NotificationMoment.format());
        let f = notification.frequency.split(" ")[1];
        let d = moment().diff(NotificationMoment) % (3600000 * f);
        console.log(d);
        if (d <= 3595000 + f * 3600000 && d >= 3660000 + f * 3600000) {
          //  NotifyNotification("Medicine", notification.note);
        }
      }
    });
  };
  const NotifyRest = () => {
    const { getAll } = useIndexedDB("rest");
    getAll().then((rests) => {
      for (const rest of rests) {
        console.log(`rest=${rest}`);
        let Wake_upMoment = moment(
          moment().format("DD/MM/YYYY") + " " + rest.wake_up,
          "DD/MM/YYYY HH:mm"
        );
        let SleepMoment = moment(
          moment().format("DD/MM/YYYY") + " " + rest.sleep,
          "DD/MM/YYYY HH:mm"
        );
        //console.log(Wake_upMoment.format());
        //console.log(SleepMoment.format());
        let d1 = Wake_upMoment.diff(moment());
        console.log(d1);
        let d2 = SleepMoment.diff(moment());
        console.log(d2);
        if (d1 <= 60000 && d1 >= -1000) {
          AcreateAndShowNotification("Wake_up", rest.wake_up);
        }
        if (d2 <= 60000 && d2 >= -1000) {
          AcreateAndShowNotification("Sleep", rest.sleep);
        }
      }
    });
  };
  const NotifyAll = () => {
    console.log("NotifyAll");
    NotifyAppointment();
    NotifyNotification();
    NotifyRest();
  };
  const { start, stop } = useInterval(NotifyAll, 60000, true);

  return (
    <Router basename={process.env.PUBLIC_URL}>
      <Switch>
        <Route exact path="/">
          <TitlePage />
        </Route>
        <Route path="/home">
          <HomePage />
        </Route>
        <Route path="/notification">
          <Notification />
        </Route>
        <Route path="/notifications">
          <NotificationList />
        </Route>
        <Route path="/health_check">
          <Health_check />
        </Route>
        <Route path="/health_checks">
          <Health_checkList />
        </Route>
        <Route path="/appointment">
          <Appointment />
        </Route>
        <Route path="/appointments">
          <AppointmentList />
        </Route>
        <Route path="/nutrition">
          <NutritionList />
        </Route>
        <Route
          path="/nutrition_subject/:nutrition"
          children={<Nutrition_subjectList />}
        />
        <Route path="/nutrition_body/:id" children={<Nutrition_body />} />
        <Route path="/ingredient">
          <IngredientList />
        </Route>
        <Route
          path="/ingredient_subject/:ingredient"
          children={<Ingredient_subjectList />}
        />
        <Route path="/ingredient_body/:id" children={<Ingredient_body />} />
        <Route path="/question">
          <QuestionList />
        </Route>
        <Route
          path="/question_subject/:question"
          children={<Question_subjectList />}
        />
        <Route path="/question_body/:id" children={<Question_body />} />

        <Route path="/water">
          <Water />
        </Route>
        <Route path="/rest">
          <Rest />
        </Route>
        <Route path="/rests">
          <Rest_next />
        </Route>
        <Route path="/bmi">
          <Bmi />
        </Route>
      </Switch>
    </Router>
  );
}
